<template>
	<view class="container">
		<view class="houseSta_top">
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="change" active-color="#03958C"></u-tabs>
		</view>
		<template v-if="isshow">
			<view v-if="current==0">
				<houseAll :current="current" :totalData="totalData" :houseTypeData="houseTypeData" :orientationData="orientationData"></houseAll>
			</view>
			<view v-else>
				<houseAll :current="current" :totalData="totalData" :houseTypeData="houseTypeData" :orientationData="orientationData"></houseAll>
			</view>
		</template>
		
		<!-- <view class="footer" @click="goBack">返回</view> -->
	</view>
</template>

<script>
	import houseAll from "@/pages/salemanagerVisual/house/components/houseAll.vue";
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				current: 0,
				isshow: false,
				list: [{
					name: '未售'
				}, {
					name: '已售'
				}],
				unSoldPercent:'', // 未售百分比
				soldPercent:'', // 已售百分比
				value1: 1,
				totalData:[], // 总数居
				houseTypeData:[], // 户型列表
				orientationData:[], // 朝向列表
			}
		},
		onLoad(){
			this._getList()
		},
		methods: {
			// 获取列表
			_getList(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('4003', Object.assign({}, {tabCode:that.current}), function (res) {
					Tools.hideLoading()
					if(res.data.code == '0'){
						that.isshow = true
						that.totalData = res.data.data.totalData
						that.houseTypeData = res.data.data.houseTypeData
						that.orientationData = res.data.data.orientationData
						that.unSoldPercent = res.data.data.unSoldPercentData
						that.soldPercent = res.data.data.soldPercentData
						that.list.forEach((item, index) => {
							if (index == 0) {
								item.name = '未售' + '(' + that.unSoldPercent + '%' + ')'
							} else {
								item.name = '已售' + '(' + that.soldPercent + '%' + ')'
							}
						})
					}
				})
			},
			// 一级tab点击
			change(index) {
				this.current = index;
				this._getList()
			},
			// 返回
			// goBack(){
			// 	uni.navigateBack({
			// 		delta:1
			// 	})
			// }
		},
		components: {
			houseAll
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}
	.container{
		.houseSta_top{
			background-color: #fff;
		}
	}
</style>
